<script>
	import { onMount, tick } from 'svelte';

	import { Game } from './classes';
	import { LoadingScreen, Menu, Intro, Outro } from './screens';

	let width;
	let view;

	let state = SKIP_MENU ? '' : 'menu';
	let loading = true;

	onMount(async () => {
		view.style.maxWidth = `${window.innerHeight * 9/16}px`;

		window.GAME = new Game(Math.min(width, window.innerHeight * 9/16), view);

		GAME.setState = (newState) => state = newState;

		await GAME.preload();

		loading = false;

		if (SKIP_MENU) GAME.start();
	});

	// $: console.log(state)
</script>

<div
	class='game'
	bind:this={view}
	bind:clientWidth={width}
>
	<canvas></canvas>
	{#if state === 'menu'}
		<Menu bind:state />
	{:else if state === 'intro'}
		<Intro bind:state />
	{:else if state === 'outro'}
		<Outro />
	{/if}
	{#if loading}
		<LoadingScreen />
	{/if}
	<div class='overlay'></div>
</div>

